import ScreenshotSlider from "./ScreenshotsSlider";
import StarRating from "@/app/components/StarRating";

type GameIdProps = {
  params: { id: string };
};

export default async function GameId({ params }: GameIdProps) {
  const [gameRes, screenshotRes] = await Promise.all([
    fetch(`https://api.rawg.io/api/games/${params.id}?key=${process.env.RAWG_API_KEY}`),
    fetch(`https://api.rawg.io/api/games/${params.id}/screenshots?key=${process.env.RAWG_API_KEY}`),
  ]);

  if (!gameRes.ok || !screenshotRes.ok) {
    throw new Error("Failed to fetch the data");
  }

  const game = await gameRes.json();
  const { results: screenshots } = await screenshotRes.json();

  return (
    <div className="min-h-screen bg-[#0a0a0a] text-gray-200 font-sans">
      {/* Game Header */}
      <div className="relative max-w-6xl mx-auto rounded-xl overflow-hidden mt-10 shadow-lg">
        <img
          src={game.background_image}
          alt={game.name}
          className="w-full h-96 object-cover opacity-30"
        />
        <div className="absolute inset-0 p-6 flex flex-col justify-end">
          <h1 className="text-4xl md:text-5xl font-bold uppercase text-white mb-4">
            {game.name}
          </h1>
          <div className="flex flex-wrap gap-3">
            {game.tags.slice(0, 6).map((tag: any) => (
              <span
                key={tag.id}
                className="border border-cyan-400 text-cyan-300 text-sm px-4 py-1 rounded-full"
              >
                {tag.name}
              </span>
            ))}
          </div>
        </div>
      </div>

      {/* Screenshot Slider */}
      <div className="max-w-6xl mx-auto mt-10">
        <ScreenshotSlider screenshots={screenshots} />
      </div>

      {/* Game Info Bar */}
      <div className="flex flex-wrap justify-between items-center text-gray-400 text-sm mt-6 max-w-6xl mx-auto px-2">
        <div className="flex items-center gap-4 text-lg">
          <span>üåê</span>
          <span>üéÆ</span>
          <span>üïπÔ∏è</span>
          <span>üëæ</span>
        </div>
        <div className="flex items-center gap-6 mt-2 md:mt-0">
          <div className="flex items-center gap-2">
            üóìÔ∏è <span>{game.released}</span>
          </div>
          <div className="flex items-center gap-2">
            üë®‚Äçüíª <span>Developer</span>
          </div>
        </div>
      </div>

      {/* Buttons */}
      <div className="flex flex-col sm:flex-row gap-6 max-w-6xl mx-auto mt-6 px-2">
        <a
          href={game.website}
          target="_blank"
          rel="noopener noreferrer"
          className="flex-1 text-center px-6 py-3 border border-cyan-500 text-cyan-300 rounded-md hover:bg-cyan-900 transition"
        >
          Visit Official Site
        </a>
        <a
          href={`https://www.reddit.com/search/?q=${encodeURIComponent(game.name)}`}
          target="_blank"
          rel="noopener noreferrer"
          className="flex-1 text-center px-6 py-3 border border-cyan-500 text-cyan-300 rounded-md hover:bg-cyan-900 transition"
        >
          Go to Reddit Page
        </a>
      </div>

      {/* Description */}
      <div className="max-w-6xl mx-auto text-sm text-gray-400 mt-8 px-2 leading-relaxed">
        <p>
          Counter-Strike is a multiplayer phenomenon in its simplicity. No
          complicated narratives to explain the source of the conflict, no
          futuristic and alien threats, but counter-terrorists against
          terrorists. Arena shooter at its core, CS:GO provides you with
          various methods of disposing enemies and reliant on cooperation
          within the team.
        </p>
        <button className="mt-4 px-4 py-2 border border-cyan-500 text-cyan-300 rounded-md hover:bg-cyan-900 transition">
          Read More
        </button>
      </div>
    </div>
  );
}
